.image-item-container {
  position: relative;
  overflow: hidden;
}

.image-title {
  position: absolute;
  top: 10px; 
  left: 10px;
  margin: 0;
  color: white;
  z-index: 1;
  font-size: 2.2vh;
}

.image-title.large {
  top: -20.7vw;/*-391px;*/ /* Adjust to position the title within the large item */
  left: 10px;
  font-size: 3vh; /* Adjust font size for large items */
}

.art {
  transition: filter 0.3s ease;
  filter: brightness(55%); 
}

.art.darken {
  filter: brightness(40%); 
}


.image-description {
  position: absolute;
  bottom: -100%; 
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5); 
  color: white;
  padding: 10px;
  transition: bottom 0.2s ease;
  box-sizing: border-box;
  font-size: 0.9vw;
}
/* Correctly hides the description for large items */
.image-description.hidden.large {
  bottom: -150%; /* Keep this consistent */
  opacity: 0; /* Ensure it's hidden */
  pointer-events: none; /* Prevents interaction when hidden */
}

/* Makes the description visible for large items */
.image-description.visible.large {
  bottom: 0; 
  opacity: 1; /* Ensure it is fully visible */
  pointer-events: auto; /* Allows interaction when visible */
}

.image-description.visible {
  bottom: 0; 
}
